:root {
  --main-text-color: #3d4e61;
  --white-color: #fff;
  --light-white-color: #f6f6f7;
  --main-border-color: #e1e7ed;
  --primary-font: 16px;
  --small-font: 14px;
}

@font-face {
  font-family: Avenir;
  font-weight: 300;
  font-style: normal;
  src: url("Avenir-Book.657c3568.ttf") format("ttf");
}

@font-face {
  font-family: Avenir;
  font-weight: 600;
  font-style: normal;
  src: url("Avenir-Heavy.01d9ab74.ttf") format("ttf");
}

* {
  box-sizing: border-box;
  padding: 0;
}

body {
  font-family: Avenir, sans-serif;
  font-size: var(--primary-font);
}

.form {
  margin: 20px auto 0;
  display: flex;
}

.form__search {
  border: 1px solid var(--main-border-color);
  color: var(--main-text-color);
  border-radius: 4px;
  outline: none;
  width: 100%;
  font-family: inherit;
  font-weight: 300;
  box-shadow: 0 1px 8px #3d4e611a;
}

.form__search--large, .form__search--small {
  background-position: center no-repeat;
  font-size: var(--primary-font);
  background-size: auto;
  line-height: 22px;
}

.form__search--large {
  height: 70px;
  padding-left: 62px;
}

.form__search--small {
  font-size: var(--small-font);
  background-position: 12px;
  background-size: 11px;
  height: 42px;
  padding-left: 33px;
}

.form__search::placeholder {
  color: inherit;
  font-weight: 400;
  line-height: 22px;
}

.form__search:focus {
  border-radius: 3px;
  font-weight: 600;
}

.form__search--large:focus, .form__search--small:focus {
  background: linear-gradient(180deg, var(--white-color) 0%, var(--light-white-color) 100%);
}

.form__search--large:focus {
  background: url("Search.ccd3c07b.svg") 25px center no-repeat, linear-gradient(180deg, var(--white-color) 0%, var(--light-white-color) 100%);
}

.form__search--small:focus {
  background: url("Search.ccd3c07b.svg") 12px center no-repeat, linear-gradient(180deg, var(--white-color) 0%, var(--light-white-color) 100%);
}

.icon {
  background: url("Search.ccd3c07b.svg") 25px no-repeat;
}

.form__search:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}
